import {graphql, Link, useStaticQuery} from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Posts from "../posts";
import "./sideReleatedNews.css"
import Img from "gatsby-image";

const Sidelist = ({url, imageurl, title}) => {
    const data = useStaticQuery(graphql`
    query {
        images: allFile {
          edges {
            node {
              relativePath
              name
              childImageSharp {
                fluid(maxWidth: 600) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
  `)
    const image = data.images.edges.find(n => {
        return n.node.relativePath.includes(imageurl);
    });
    function createImage(){
        return data.images.edges.filter(item=>item.node.relativePath === imageurl)
    }
    return(

    <li className="css-qt919d"><a className="css-o0bb22"
                                  href={"/articulos/" + url}>
        <div style={{height:'100px',width:'100px'}}>
            <Img style={{height:'75px',width:'100px'}} fluid={createImage()[0].node.childImageSharp.fluid} />
        </div>

    </a>
        <div className="css-bufxhs">
            <a className="css-1ixx31q" href={"/articulos/" + url}>
                {title}
            </a>
        </div>
    </li>)
}

export default Sidelist